import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import ImageResults from './ImageResults'

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: '',
      amount: 10,
      apiURL: 'https://pixabay.com/api',
      apiKey: '8913420-89429fef6031f24fd40903778',
      images: []
    };
    this.onSearchTextChange = this.onSearchTextChange.bind(this);
    this.onQtyChange = this.onQtyChange.bind(this)
  }

  fetchRequest() {
    return fetch(`${this.state.apiURL}/?key=${this.state.apiKey}&q=${this.state.searchText}
    &image_type=photo&per_page=${this.state.amount}&safesearch=true`)
    .then(response => response.json())
    .then(response => this.setState({images: response.hits}))
  }

  onSearchTextChange(e) {
    const val = e.target.value;
    this.setState({
      [e.target.name]: val}, () => {
      if(val === '') {
        this.setState({images: []})
      } else {
        this.fetchRequest() 
      }
      }
    )
  }

  onQtyChange(e, index, value) {
    this.setState({
      amount: value
    })
  }

  render() {
    return (
      <div>
        <TextField 
          name='searchText'
          value={this.state.searchText}
          onChange={this.onSearchTextChange}
          floatingLabelText='input text for picture search'
          fullWidth={true}
        />
        <SelectField 
          name='amount'
          value={this.state.amount}
          onChange={this.onQtyChange}
          floatingLabelText='qty'
        >
          <MenuItem value={3} primaryText='3' />   
          <MenuItem value={5} primaryText='5' />
          <MenuItem value={10} primaryText='10' />
          <MenuItem value={25} primaryText='25' />
          <MenuItem value={50} primaryText='50' />
        </SelectField>
        {this.state.images.length > 0 ? <ImageResults images = {this.state.images}/> : null}
      </div>
    )
  }
}

export default Search


